import { useEffect, useState } from 'react';
import { shortenAddress } from '@usedapp/core';

// ----------------------------------------------------------------------

// material
import {
  Table,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
  TableContainer,
  Tooltip,
  TablePagination,
  Link,
  Typography,
  Stack,
  Chip
} from '@material-ui/core';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import ErrorIcon from '@material-ui/icons/Error';
import { useQuery } from '@apollo/client';
import { GET_PAST_JACKPOTS_APESLOTS } from '../../queries';
import { LOTO_ADDRESS_APESLOTS } from '../../config';
import { hexToBytes, getSymbolPlaces } from '../../utils/encodeParams';
// components
import Scrollbar from '../Scrollbar';

function formatHashData(hash) {
  const bytes1 = hexToBytes(hash);
  const symbols1 = bytes1.map((row) => getSymbolPlaces(row));

  const hashData = [];

  for (let i = 0; i < 6; i += 1) {
    const symbol1 = {
      symbol: '',
      match: false
    };

    symbol1.symbol = symbols1[i];

    if (
      (i === 5 && symbol1.symbol === 'A') ||
      (i === 5 && symbol1.symbol === 'B') ||
      (i === 5 && symbol1.symbol === 'C') ||
      (i === 5 && symbol1.symbol === 'D') ||
      (i === 5 && symbol1.symbol === 'E')
    ) {
      symbol1.match = true;
    }

    hashData.push(symbol1);
  }

  return { hashData };
}

const MYBETSCOLUMNS = [
  { id: 'block', label: 'Block', minWidth: 50 },
  { id: 'betid', label: 'Bet ID', minWidth: 50 },
  { id: 'user', label: 'User', minWidth: 100 },
  { id: 'hash', label: 'Hash', minWidth: 50 },
  { id: 'mode', label: 'Mode', minWidth: 50 },
  { id: 'betamount', label: 'Bet Amount', minWidth: 50, align: 'right' },
  { id: 'winamount', label: 'Win Amount', minWidth: 50, align: 'right' }
];

// ----------------------------------------------------------------------

export default function GroupingFixedHeader(props) {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [bets, setBets] = useState([]);
  const [total, setTotal] = useState(0);

  const { data } = useQuery(GET_PAST_JACKPOTS_APESLOTS, {
    variables: {
      perPage: rowsPerPage,
      skip: page * rowsPerPage,
      status: true
    },
    pollInterval: 3000
  });

  useEffect(() => {
    if (data) {
      const betsData = data.bets.map((row) => ({
        betid: row.betID,
        block: row.block,
        betstatus: row.win,
        hash: formatHashData(row.hash),
        bet: row.mode,
        betamount: row.amount,
        winamount: row.reward,
        user: row.user.id,
        txid: row.tx
      }));

      setBets(betsData);

      if (data.summaries.length > 0) {
        setTotal(data.summaries[0].totalJackpotsApeSlots);
      }
    }
  }, [data]);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <>
      <Scrollbar>
        <TableContainer sx={{ minWidth: '65rem', maxHeight: '35rem', minHeight: '35rem' }}>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                {MYBETSCOLUMNS.map((column) => (
                  <TableCell
                    key={column.id}
                    align={column.align}
                    style={{ top: 0, minWidth: column.minWidth, boxShadow: 'none' }}
                    sx={{ background: '#291104', fontSize: '1rem' }}
                  >
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>

            <TableBody style={{ top: 40 }}>
              {bets.map((row) => (
                <TableRow hover role="checkbox" tabIndex={-1} key={row.block + row.winner}>
                  {MYBETSCOLUMNS.map((column) => {
                    const value = row[column.id];

                    // BLOCK
                    if (column.id === 'block') {
                      if (row.betstatus === true) {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              <Tooltip
                                title={<h3 style={{ fontWeight: 400 }}>Bet played successfully and jackpot won </h3>}
                                arrow
                                placement="top"
                              >
                                <CheckCircleIcon
                                  color="success"
                                  style={{ verticalAlign: 'middle', marginRight: '0.2rem' }}
                                />
                              </Tooltip>
                              <Link
                                href={`https://bscscan.com/block/${value}`}
                                style={{ color: 'white' }}
                                target="_blank"
                              >
                                {value}
                              </Link>
                            </Typography>
                          </TableCell>
                        );
                      }

                      if (row.betstatus === false) {
                        return (
                          <TableCell key={column.id} align={column.align}>
                            <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                              <Tooltip
                                title={<h3 style={{ fontWeight: 400 }}>Bet played successfully without jackpot </h3>}
                                arrow
                                placement="top"
                              >
                                <ErrorIcon color="error" style={{ verticalAlign: 'middle', marginRight: '0.2rem' }} />
                              </Tooltip>
                              <Link
                                href={`https://bscscan.com/block/${value}`}
                                style={{ color: 'white' }}
                                target="_blank"
                              >
                                {value}
                              </Link>
                            </Typography>
                          </TableCell>
                        );
                      }
                    }

                    if (column.id === 'user') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            <Link
                              href={`https://bscscan.com/address/${row.user}`}
                              target="_blank"
                              style={{ color: 'white' }}
                            >
                              {shortenAddress(row.user)}
                            </Link>
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'betid') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            <Link
                              href={`https://bscscan.com/address/${LOTO_ADDRESS_APESLOTS}#readContract`}
                              target="_blank"
                              style={{ color: 'white' }}
                            >
                              {value}
                            </Link>
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'hash') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            {row.hash.hashData.map((hash, i) => (
                              <Typography style={{ display: 'inline-block' }} color={hash.match ? 'primary' : ''}>
                                {hash.symbol}
                              </Typography>
                            ))}
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'mode') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            {row.mode ? 'Degen' : 'Normal'}
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'betamount') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            <Stack direction="row" spacing={1} justifyContent="flex-end" alignItems="center">
                              <Link
                                href={`https://bscscan.com/tx/${row.txid}`}
                                target="_blank"
                                style={{ color: 'white' }}
                              >
                                {value} BNB
                              </Link>
                              <img alt="" src="/static/icons/ic_bnb.svg" width={20} height={20} />
                            </Stack>
                          </Typography>
                        </TableCell>
                      );
                    }

                    if (column.id === 'winamount') {
                      return (
                        <TableCell key={column.id} align={column.align}>
                          <Typography variant="subtitle1" style={{ fontWeight: 400 }}>
                            <Stack direction="row" spacing={1} justifyContent="flex-end" alignItems="center">
                              <Link
                                href={`https://bscscan.com/tx/${row.txid}`}
                                target="_blank"
                                style={{ color: 'white' }}
                              >
                                {value} BNB
                              </Link>
                              <img alt="" src="/static/icons/ic_bnb.svg" width={20} height={20} />
                            </Stack>
                          </Typography>
                        </TableCell>
                      );
                    }

                    return '';
                  })}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Scrollbar>
      <TablePagination
        page={page}
        component="div"
        count={Number(total)}
        rowsPerPage={rowsPerPage}
        onPageChange={handleChangePage}
        rowsPerPageOptions={[10, 25, 50]}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </>
  );
}
