function importAll(r) {
  return r.keys().map(r);
}

const wideimages = importAll(require.context('./wide', false, /\.(png|jpe?g|svg)$/));
const potraitimages = importAll(require.context('./potrait', false, /\.(png|jpe?g|svg)$/));
const images = { wide: wideimages, potrait: potraitimages };
export default { images };
