import { useState } from 'react';
import Lottie from 'react-lottie';
import { motion } from 'framer-motion';

// material
import {
  Button,
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  Stack,
  Paper,
  styled,
  Typography,
  Link
} from '@material-ui/core';

import WinnerSpin from '../../assets/cryptodrop/lottie/winnerspin.json';

import CoinsExplode from '../../assets/cryptodrop/lottie/coinsExplode.json';
import RedConfetti from '../../assets/cryptodrop/lottie/redConfetti.json';

import Trophy from '../../assets/cryptodrop/lottie/trophy.json';
import LoseGame from '../../assets/cryptodrop/lottie/losegame.json';
import { varFadeIn, varFadeInUp, varWrapEnter, varFadeInRight, varZoomIn, varZoomOut } from '../animate';

import DancingMonkey from '../../assets/apeslots/dancingmonkey.json';
import Lights from '../../assets/apeslots/lights.json';

// ----------------------------------------------------------------------

const AnimateContainer = styled(motion.div)(({ theme }) => ({}));

const winnerSpinOptions = {
  loop: true,
  autoplay: true,
  animationData: Lights,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const coinsExplodeOptions = {
  loop: true,
  autoplay: true,
  animationData: CoinsExplode,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const trophyOptions = {
  loop: true,
  autoplay: true,
  animationData: DancingMonkey,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const redConfettiOptions = {
  loop: true,
  autoplay: true,
  animationData: RedConfetti,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

const loseGameOptions = {
  loop: true,
  autoplay: true,
  animationData: LoseGame,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice'
  },
  background: 'transparent'
};

export default function ApeSlotsResult(props) {
  const handleClose = () => {
    props.setOpenResult(false);
  };

  return (
    <div style={{ width: '100%' }}>
      <Dialog open={props.openResult} style={{ maxWidth: '35rem', marginInline: 'auto' }}>
        {props.totalPrize > 0 && (
          <Lottie
            options={winnerSpinOptions}
            style={{ width: '30rem', height: '30rem', position: 'absolute' }}
            isClickToPauseDisabled
          />
        )}

        <AnimateContainer
          initial="initial"
          animate="animate"
          variants={varWrapEnter}
          style={{ border: '4px solid #ff9f00', overflow: 'hidden', borderRadius: '1rem' }}
        >
          <DialogContent style={{ padding: '0.2rem', overflow: 'hidden', backgroundColor: '#1F0C02' }}>
            <Stack direction="column" style={{ minWidth: '28rem', minHeight: '25rem', padding: '2rem' }}>
              <Stack direction="column" justifyContent="center" alignItems="center" marginBottom="0.5rem">
                {props.totalPrize > 0 ? (
                  <Lottie
                    options={trophyOptions}
                    style={{ width: '22rem', height: '24rem', marginTop: '-2rem', marginBottom: '-5rem' }}
                    isClickToPauseDisabled
                  />
                ) : (
                  <>
                    <Typography variant="h3" style={{ marginTop: '1rem' }}>
                      Try Again
                    </Typography>
                    <Lottie
                      options={loseGameOptions}
                      style={{ width: '10rem', height: '10rem' }}
                      isClickToPauseDisabled
                    />
                  </>
                )}

                {props.totalPrize > 0 ? (
                  <>
                    <Typography variant="h3" sx={{ mt: 5 }}>
                      You Win
                    </Typography>
                    <Typography variant="h2" className="pulse">
                      {props.totalPrize} BNB
                    </Typography>
                    <Typography variant="subtitle1" color="#866767">
                      Coins are already in your wallet
                    </Typography>
                  </>
                ) : (
                  <Typography variant="h5" color="#866767" style={{ marginTop: '1rem' }}>
                    Better luck next time!
                  </Typography>
                )}

                <Link
                  href={`https://bscscan.com/tx/${props.hash.completeLastHash}`}
                  target="_blank"
                  style={{ color: 'white', zIndex: '3' }}
                >
                  {`TX: ${props.hash.startHash}...${props.hash.endHash}${props.hash.lastSixHash}`}
                </Link>

                {props.totalPrize > 0 && (
                  <Lottie
                    options={redConfettiOptions}
                    style={{ position: 'absolute' }}
                    height="100%"
                    width="100%"
                    isClickToPauseDisabled
                  />
                )}
              </Stack>
            </Stack>
          </DialogContent>
          <DialogActions style={{ minHeight: '5rem', backgroundColor: '#1F0C02' }}>
            <Button onClick={handleClose} autoFocus style={{ zIndex: '3' }} color="yellow">
              Close
            </Button>
          </DialogActions>
        </AnimateContainer>
      </Dialog>
    </div>
  );
}
