import { useState, useEffect, useRef } from 'react';
import { Typography, Stack, Button, Box, Divider, Switch, FormControlLabel } from '@material-ui/core';
import { motion } from 'framer-motion';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { styled, useTheme } from '@material-ui/core/styles';
import { useEthers, useContractCalls, useContractFunction, useBlockNumber, useContractCall } from '@usedapp/core';
import { utils, BigNumber } from 'ethers';

import { Helmet } from 'react-helmet-async';
import useSound from 'use-sound';

import ApeSlotResult from './ApeSlotsResult';
import sliderChangeSound from '../../assets/sfx/sliderChange.mp3';
import slotStartSound from '../../assets/apeslots/startGame.mp3';
import spinningSlot from '../../assets/apeslots/spinningSlot.wav';
import slotLoseSound from '../../assets/apeslots/slotLoseSound.mp3';
import slotWinSound from '../../assets/apeslots/slotWin.mp3';

import losingSlot from '../../assets/sfx/slots/losingSlot.mp3';
import degenMusic from '../../assets/apeslots/degenMusic.wav';

import MiningNotifSound from '../../assets/mining/miningNotifSound.mp3';

import { encodeCombination, createBetId, placeSymbols, hexToBytes, getSymbolPlaces } from '../../utils/encodeParams';

import Label from '../Label';
import { varFadeInUp } from '../animate';

import {
  LOTO_APESLOTS_CONTRACT,
  LOTO_APESLOTS_INTERFACE,
  LOTO_ADDRESS_APESLOTS,
  ROUTER_INTERFACE,
  PANCAKE_ROUTER_V2_ADDRESS,
  WBNB_ADDRESS,
  LINK_ADDRESS,
  LINK_FEE
} from '../../config';
import { fCRCurrency, fMCurrency } from '../../utils/formatNumber';
import MiningSnackBar from '../snackbar/MiningSnackbar';

const SlotsWrapperStyle = {
  // minHeight: '200px',
  // padding: '1rem',
  borderRadius: '0.8rem',
  // background: 'url(/static/background/slots-background.png)',
  maxWidth: '100%',
  marginInline: 'auto',
  background:
    'linear-gradient(rgb(74 36 7) 0%, rgb(254 218 161) 15%, rgb(255 244 197) 46%, rgb(230, 218, 195) 92%, rgb(232, 189, 151) 100%) center bottom / cover',
  backgroundSize: 'cover',
  backgroundPosition: 'bottom',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  boxShadow: 'inset 0px 0px 10px 0px rgba(0,0,0,1)',
  border: '4px solid #ff9f00',
  position: 'relative'
};

const dividerWrapperStyle = {
  position: 'relative',
  width: '1.5rem',
  height: '120%',
  marginLeft: '-0.75rem',
  marginRight: '-0.75rem'
};

const dividerStyle = {
  height: '120%',
  width: '1.5rem',
  marginTop: '-1rem',
  position: 'absolute'
};

let winningStateStatus = true;
// const winningType = "win3"

const regularBet = 0.01;
const degenBet = 0.025;

const ApeSlotsGame = (props) => {
  const sleep = (milliseconds) => {
    console.info('...');
    return new Promise((resolve) => setTimeout(resolve, milliseconds));
  };

  const [playMiningNotifSound, { stopMiningNotifSound }] = useSound(MiningNotifSound);
  const [showMiningSnackbar, setShowMiningSnackbar] = useState(false);
  const [minedAmount, setMinedAmount] = useState(1000);
  const handleMiningSnackbar = async (minedAmountMessage) => {
     sleep(2000);
    setMinedAmount(minedAmountMessage);
    playMiningNotifSound();
    setShowMiningSnackbar(true);
     sleep(5000);
    setShowMiningSnackbar(false);
  };

  // SFX
  const [pageVolume, setVolume] = useState(1);
  const [soundToggle, setSoundToggle] = useState({ status: true, message: 'Sound On' });
  const [playSliderChange, { stopSliderChange }] = useSound(sliderChangeSound, { volume: pageVolume });
  const [playSlotStartSound, { stopSlotStartSound }] = useSound(slotStartSound, { volume: pageVolume });

  const [playSpinningSlotSound, { stop: stopSpinningSound }] = useSound(spinningSlot, {
    volume: pageVolume,
    loop: true
  });

  const [playSlotLoseSound, { stopSlotLoseSound }] = useSound(slotLoseSound, { volume: pageVolume });
  const [playSlotWinSound, { stopSlotWinSound }] = useSound(slotWinSound, { volume: pageVolume });

  const [losingSlotPlay, { stopLosingSlot }] = useSound(losingSlot, { volume: pageVolume });

  const [degenMusicPlay, { stop: stopDegenMusic }] = useSound(degenMusic, { volume: pageVolume / 6, loop: true });

  const formatLastHash = (lastHashString) => {
    const startHashSlice = lastHashString.slice(0, 5);
    const endHashSlice = lastHashString.slice(lastHashString.length - 15, -6);
    const lastSixHashSlice = lastHashString.slice(lastHashString.length - 6);

    return {
      startHash: startHashSlice,
      endHash: endHashSlice,
      lastSixHash: lastSixHashSlice,
      completeLastHash: lastHashString
    };
  };

  const initialLastHash = formatLastHash('');

  const [lastHash, setLastHash] = useState(initialLastHash);

  useEffect(() => {
    console.log('stop music');
    return () => {
      stopDegenMusic();
    };
  }, [stopDegenMusic]);

  const handleSoundToggle = () => {
    if (soundToggle.status === true) {
      setSoundToggle({ status: false, message: 'Sound Off' });
      setVolume(0);
    } else {
      setSoundToggle({ status: true, message: 'Sound On' });
      setVolume(1);
    }
  };

  const handleDegenMode = () => {
    console.log('degen mode switch');

    if (!placeBet.disablebutton) {
      if (props.bananaRain === true) {
        props.setBananaRain(false);
        setDegenMode(false);
        stopDegenMusic();
      } else {
        props.setBananaRain(true);
        setDegenMode(true);
        degenMusicPlay();
      }
    }
  };

  const playLoseSlot = () => {
    losingSlotPlay();
  };

  // WALLET ACCOUNT
  const { account } = useEthers();

  const handleUnconnected = () => {
    setOpen(true);
  };

  // Unconnected Alert
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };

  const theme = useTheme();
  const bigdesktop = useMediaQuery(theme.breakpoints.up('xl'));
  const desktop = useMediaQuery(theme.breakpoints.up('lg'));
  const tablet = useMediaQuery(theme.breakpoints.up('md'));
  const mobile = useMediaQuery(theme.breakpoints.up('sm'));
  const smallMobile = useMediaQuery(theme.breakpoints.up('xs'));

  const slotItemStyle = {
    width: '100%',
    maxWidth: '200px',
    marginInline: 'auto'
  };

  // BET AMOUNT
  const [amount, setAmount] = useState(regularBet);
  const amountRef = useRef(amount);
  const [vrfFee, setVrfFee] = useState(utils.parseEther('0'));

  const setAmountRef = (data) => {
    amountRef.current = data;
    setAmount(data);
  };

  const [betInfo, setBetInfo] = useState({
    hash: '',
    reward: 0
  });

  const betInfoRef = useRef(betInfo);
  const setBetInfoRef = (data) => {
    betInfoRef.current = data;
    setBetInfo(data);
  };

  // DEGEN STATE
  const [degenMode, setDegenMode] = useState(false);
  useEffect(() => {
    if (degenMode) {
      setAmountRef(degenBet);
    } else {
      setAmountRef(regularBet);
    }
  }, [degenMode]);

  const handleBet = () => {
    alert('bet');
  };

  const [winAmount, setWinAmount] = useState({
    visibility: 'visible',
    amount: 1
  });

  const [betId, setBetId] = useState('');

  const [betIdSetuped, setBetIdSetuped] = useState(false);

  const betIdData = useContractCall({
    abi: LOTO_APESLOTS_INTERFACE,
    address: LOTO_ADDRESS_APESLOTS,
    method: 'getBetsLength',
    args: [account]
  });

   useEffect( () => {
    if (betIdData && !betIdSetuped) {
      setBetId(betIdData);
      setBetIdSetuped(true);
    }
  }, [betIdData, betIdSetuped]);

  const { state, send } = useContractFunction(LOTO_APESLOTS_CONTRACT, 'placeBet', {
    transactionName: 'Ape Slots: Place Bet'
  });

   useEffect( () => {
    if ((state.status === 'Exception' || state.status === 'Fail') && isStartSpin) {
      setGameResult({ totalPrize: 0, hash: '' });
      stopSpin('lose');
      setIsStartSpin(false);
      setLastHash(initialLastHash);
    }

    if (state.status === 'None' && isStartSpin) {
      setGameResult({ totalPrize: 0, hash: '' });
      stopSpin('lose');
      setIsStartSpin(false);
      setLastHash(initialLastHash);
    }

    if (state.status === 'Success') {
      const hash = formatLastHash(state.transaction.hash);

      setLastHash(hash);

      setIsStartSpin(false);
    }
  }, [state]);

  const [isMatch, setMatch] = useState(false);

  const betDataInfo = useContractCalls([
    {
      abi: LOTO_APESLOTS_INTERFACE,
      address: LOTO_ADDRESS_APESLOTS,
      method: 'bets',
      args: [account, betId.toString()]
    },
    {
      abi: ROUTER_INTERFACE,
      address: PANCAKE_ROUTER_V2_ADDRESS,
      method: 'getAmountsIn',
      args: [utils.parseEther(LINK_FEE), [WBNB_ADDRESS, LINK_ADDRESS]]
    },
    {
      abi: LOTO_APESLOTS_INTERFACE,
      address: LOTO_ADDRESS_APESLOTS,
      method: 'miningAmount',
      args: []
    },
    {
      abi: LOTO_APESLOTS_INTERFACE,
      address: LOTO_ADDRESS_APESLOTS,
      method: 'checkMiningAvailability',
      args: []
    }
  ]);

   useEffect( () => {
    if (betDataInfo[0]) {
      if (betDataInfo[0].hash !== '0x' && betDataInfo[0].hash !== '0x000000000000' && !isMatch) {
        console.log(betDataInfo);

        setBetInfoRef({
          reward: utils.formatEther(betDataInfo[0].reward),
          hash: hexToBytes(betDataInfo[0].hash)
        });

        setMatch(true);

        console.log('hex data');
        console.log(hexToBytes(betDataInfo[0].hash));

        console.log('fm currency');
        console.log(fMCurrency(utils.formatEther(betDataInfo[2].toString())));

        if (betDataInfo[3] && betDataInfo[3][0]) {
          handleMiningSnackbar(fMCurrency(utils.formatEther(betDataInfo[2].toString())));
        }

        if (betDataInfo[0].reward.gt(utils.parseEther('0'))) {
          setGameResult({
            totalPrize: fCRCurrency(utils.formatEther(betDataInfo[0].reward.toString())),
            hash: lastHash
          });

          const bytes = hexToBytes(betDataInfo[0].hash);
          const last = getSymbolPlaces(bytes[5]);

          if (last === 'A') {
            stopSpin('win1');
          }

          if (last === 'B') {
            stopSpin('win2');
          }

          if (last === 'C') {
            stopSpin('win3');
          }

          if (last === 'D') {
            stopSpin('win4');
          }

          if (last === 'E') {
            stopSpin('win5');
          }
        } else {
          setGameResult({ totalPrize: 0, hash: lastHash });

          stopSpin('lose');
        }

        setBetId('');
        setBetIdSetuped(false);
      }
    }

    if (betDataInfo[1]) {
      const [amounts] = betDataInfo[1].amounts;

      if (!amounts.eq(vrfFee)) {
        setVrfFee(amounts.add(amounts.mul(BigNumber.from('10')).div(BigNumber.from('100'))));
      }
    }
  }, [betDataInfo[0], betDataInfo[1], isMatch]);

  const [placeBet, setPlaceBet] = useState({
    errorstatus: 0,
    disablebutton: 1,
    message: '',
    onClick: handleBet
  });
  const [disableInput, setDisableInput] = useState(false);

  useEffect(() => {
    if (account) {
      setPlaceBet({ ...placeBet, onClick: startSpin, disablebutton: 0 });
    } else {
      setPlaceBet({ ...placeBet, onClick: handleUnconnected });
    }
  }, [account]);

  function valuetext(value) {
    return `$${value}BNB`;
  }

  const slotItems = useRef(null);

  // const [winState, setWinState] = useState(false);
  const [openResult, setOpenResult] = useState(false);
  const [slotEndMotion, setSlotEndMotion] = useState('');
  const [slotItemEndMotion, setSlotItemEndMotion] = useState('');
  const [gameResult, setGameResult] = useState({ totalPrize: 0, hash: '' });

  const stopSpin = async (result) => {
    if (result.includes('win')) {
      winningStateStatus = true;
    } else if (result === 'lose') {
      winningStateStatus = false;
    }

    if (winningStateStatus === true) {
      window.stopSpin(result, async () => {
        console.log('stop spin');
      });
    } else {
      window.stopSpin('lose', async () => {
        console.log('stop spin');
      });
    }
  };

  const [isStartSpin, setIsStartSpin] = useState(false);

  const startSpin = async () => {
    setMatch(false);

    setIsStartSpin(true);

    if (!placeBet.disablebutton) {
      let mode = 0;

      if (degenMode) {
        mode = 1;
      }
      send(mode, vrfFee, { value: utils.parseEther(amount.toString()).add(vrfFee), gasLimit: 2000000 });
      setPlaceBet({ ...placeBet, disablebutton: 1 });
      setDisableInput(true);
      // setSlotEndMotion('animate__animated animate__bounce animate__fast');

      playSlotStartSound();
      playSpinningSlotSound();

      const winStatus = false;
      // const winStatus = false;
      // const winStatus = true;

      window.startSpin('win', soundToggle.status, async () => {
        if (winningStateStatus === true) {
          stopSpinningSound();

           sleep(100);
          setSlotEndMotion('animate__animated animate__rubberBand');
          setSlotItemEndMotion('animate__animated animate__flash');

           sleep(500);
          playSlotWinSound();
          setOpenResult(true);

          setSlotEndMotion('');
          setSlotItemEndMotion('');

          setPlaceBet({ ...placeBet, disablebutton: 0 });
          setDisableInput(false);
        } else if (winningStateStatus === false) {
          stopSpinningSound();

          setSlotEndMotion('animate__animated animate__headShake');

           sleep(400);
          playSlotLoseSound();
           sleep(200);
          setOpenResult(true);

          setSlotEndMotion('');

          setPlaceBet({ ...placeBet, disablebutton: 0 });
          setDisableInput(false);
        }
      });
    }
  };

  return (
    <>
      <Helmet>
        <script src="/js/apeslots.js" type="text/javascript" />
      </Helmet>

      <MiningSnackBar visible={showMiningSnackbar} amount={minedAmount} />

      <ApeSlotResult
        openResult={openResult}
        setOpenResult={setOpenResult}
        totalPrize={gameResult.totalPrize}
        hash={lastHash}
        // betAmount={amount}
        // playResult={playResult}
        // totalPrize={totalPrize}
        // initializePlay={initializePlay}
        // lastHash={lastHash}
      />

      <Stack direction="row" style={{ marginBottom: '2rem' }}>
        <img
          src="/static/apeslots/ApeslotsBadge.svg"
          alt="Ape Slots"
          className="apeslots-badge"
          style={{ width: '60%', marginTop: '-6rem' }}
        />
        <div
          className={`degen-btn ${!degenMode && 'degen-btn-inactive'} ${placeBet.disablebutton && 'pointer-none'}`}
          onClick={handleDegenMode}
          role="button"
          tabIndex={0}
          onKeyDown={handleDegenMode}
        />
      </Stack>

      <motion.div variants={varFadeInUp} style={{ display: 'flex' }}>
        <Box style={SlotsWrapperStyle} className={`${slotEndMotion} ${degenMode && 'degen-blink'}`}>
          <object
            type="image/svg+xml"
            data="/static/apeslots/leaves-l.svg"
            style={{ width: '10%', zIndex: '2', position: 'absolute', marginBottom: '-12%', left: '-5%' }}
          >
            l
          </object>
          <Stack style={{ width: '100%' }}>
            <Stack direction="row" ref={slotItems} className="apeslotwrapper" id="example5">
              {/* SLOT 1 */}
              <ul>
                <li>
                  <img src="/static/apeslots/slotItemA.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemB.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemC.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemD.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemE.png" alt="" style={slotItemStyle} />
                </li>
              </ul>

              <div className="leaf-divider-wrapper">
                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider">
                  lf
                </object>

                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider-bottom">
                  lf
                </object>
              </div>

              {/* SLOT 2 */}
              <ul>
                <li>
                  <img src="/static/apeslots/slotItemA.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemB.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemC.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemD.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemE.png" alt="" style={slotItemStyle} />
                </li>
              </ul>

              <div className="leaf-divider-wrapper">
                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider">
                  lf
                </object>

                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider-bottom">
                  lf
                </object>
              </div>

              {/* SLOT 3 */}
              <ul>
                <li>
                  <img src="/static/apeslots/slotItemA.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemB.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemC.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemD.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemE.png" alt="" style={slotItemStyle} />
                </li>
              </ul>

              <div className="leaf-divider-wrapper">
                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider">
                  lf
                </object>

                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider-bottom">
                  lf
                </object>
              </div>

              {/* SLOT 4 */}
              <ul>
                <li>
                  <img src="/static/apeslots/slotItemA.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemB.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemC.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemD.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemE.png" alt="" style={slotItemStyle} />
                </li>
              </ul>

              <div className="leaf-divider-wrapper">
                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider">
                  lf
                </object>

                <object type="image/svg+xml" data="/static/apeslots/leaf-divider.svg" className="leaf-divider-bottom">
                  lf
                </object>
              </div>

              {/* SLOT 5 */}
              <ul>
                <li>
                  <img src="/static/apeslots/slotItemA.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemB.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemC.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemD.png" alt="" style={slotItemStyle} />
                </li>
                <li>
                  <img src="/static/apeslots/slotItemE.png" alt="" style={slotItemStyle} />
                </li>
              </ul>
            </Stack>
          </Stack>
          <object
            type="image/svg+xml"
            data="/static/apeslots/leaves-r.svg"
            style={{ width: '10%', zIndex: '2', position: 'absolute', marginBottom: '-12%', right: '-5%' }}
          >
            l
          </object>
        </Box>
      </motion.div>

      <Divider variant="middle" style={{ marginBottom: '2rem', marginTop: '2rem' }} />

      <Stack direction="row" style={{ flexWrap: 'wrap', alignItems: 'center' }}>
        <Stack alignItems="start" style={{ margin: '0.5rem', marginRight: '1rem', width: '8rem' }}>
          <Typography variant="h4" component="h1" paragraph style={{ marginLeft: '5%', marginBottom: '0.1rem' }}>
            Your Bet
          </Typography>

          {degenMode ? (
            <Label variant="filled" color="yellow" style={{ marginLeft: '5%', width: 'min-content' }}>
              DEGEN MODE
            </Label>
          ) : (
            <Label variant="filled" color="yellow" style={{ marginLeft: '5%', width: 'min-content' }}>
              REGULAR MODE
            </Label>
          )}

          <Label color="yellow" style={{ fontSize: '1rem', marginTop: '0.4rem', marginLeft: '5%' }}>
            VRF: {fCRCurrency(utils.formatEther(vrfFee))}
          </Label>
        </Stack>

        <Typography variant="h3" style={{ marginLeft: '1rem', color: '#ffc900' }}>
          {!degenMode ? `${regularBet} BNB` : `${degenBet} BNB`}
        </Typography>

        {/* <Box sx={{ mt: 2, mb: 2, display: 'flex', flexGrow: '1' }} style={{ margin: '0.5rem' }}>
                    <TextField
                        disabled
                        style={{ width: '90%', margin: 'auto' }}
                        error={placeBet.errorstatus}
                        helperText={placeBet.message}
                        label="Bet Amount"
                        InputProps={{
                            endAdornment: <InputAdornment position="end">BNB</InputAdornment>,
                            inputProps: {
                            max: 100,
                            min: 10
                            }
                        }}
                        type="number"
                        value={amount}
                        onChange={(e) => handleSliderChange(e.target.value)}
                    />
                </Box> */}
      </Stack>

      {/* <Box sx={{ mt: 2, mb: 2, display: 'flex' }}>
                <Slider
                    disabled={disableInput}
                    value={amount}
                    color="yellow"
                    getAriaValueText={valuetext}
                    valueLabelDisplay="auto"
                    step={!degenMode ? 0.0245 : 0.02}
                    marks
                    min={!degenMode ? 0.01 : 0.1}
                    max={0.5}
                    style={{ width: '90%', margin: 'auto' }}
                    onChange={(e) => handleSliderChange(e.target.value)}
                />
            </Box> */}

      <FormControlLabel
        label={soundToggle.message}
        labelPlacement="start"
        style={{ width: 'fit-content', marginLeft: 'auto' }}
        control={<Switch color="yellow" checked={soundToggle.status} onChange={handleSoundToggle} />}
      />

      <div
        className={`apeslots-bet-btn ${placeBet.disablebutton && 'apeslots-bet-disabled'}`}
        onClick={!placeBet.disablebutton && startSpin}
        role="button"
        tabIndex={0}
        onKeyDown={startSpin}
      />
    </>
  );
};

export default ApeSlotsGame;
