import faker from 'faker';
import Slider from 'react-slick';
import PropTypes from 'prop-types';
import { useState, useRef } from 'react';

// material
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { Box, Card } from '@material-ui/core';

// utils
import images from '../../assets/cryptodropbanner';
import imagesvip from '../../assets/cryptodropbannervip';
//
import { CarouselControlsArrowsIndex } from './controls';

// ----------------------------------------------------------------------

CarouselItem.propTypes = {
  item: PropTypes.object
};

function CarouselItem({ item }) {
  const { image, title } = item;

  return <Box component="img" alt={title} src={image} sx={{ width: '100%', height: '100%', objectFit: 'cover' }} />;
}

const SimpleBannerCarousel = (props) => {
  const theme = useTheme();
  const carouselRef = useRef();

  const bigdesktop = useMediaQuery(theme.breakpoints.up('xl'));
  const desktop = useMediaQuery(theme.breakpoints.up('lg'));
  const tablet = useMediaQuery(theme.breakpoints.up('md'));
  const mobile = useMediaQuery(theme.breakpoints.up('sm'));
  const smallMobile = useMediaQuery(theme.breakpoints.up('xs'));

  const bannerSize = () => {
    if (props.horizontalOnly) {
      return '15rem';
    }

    if (bigdesktop) {
      return '100%';
    }
    if (desktop) {
      return '15rem';
    }
    if (tablet) {
      return '15rem';
    }
    if (mobile) {
      return '15rem';
    }
    if (smallMobile) {
      return '15rem';
    }
  };

  const imagesData = !props.vip ? images.images : imagesvip.images;

  let CAROUSELS = null;
  CAROUSELS = imagesData.potrait.map((img, index) => {
    if (props.horizontalOnly) {
      return {
        image: imagesData.wide[index].default
      };
    }

    if (bigdesktop) {
      return {
        image: img.default
      };
    }
    return {
      image: imagesData.wide[index].default
    };
  });

  const [currentIndex, setCurrentIndex] = useState(theme.direction === 'rtl' ? CAROUSELS.length - 1 : 0);

  const settings = {
    speed: 500,
    autoplaySpeed: 7000,
    dots: false,
    arrows: false,
    autoplay: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    rtl: Boolean(theme.direction === 'rtl'),
    beforeChange: (current, next) => setCurrentIndex(next)
  };

  const handlePrevious = () => {
    carouselRef.current.slickPrev();
  };

  const handleNext = () => {
    carouselRef.current.slickNext();
  };

  return (
    <Card style={{ height: bannerSize() }}>
      <Slider ref={carouselRef} {...settings} style={{ height: '100%' }}>
        {CAROUSELS.map((item, index) => (
          <CarouselItem key={index} item={item} style={{ height: '100%' }} />
        ))}
      </Slider>

      {/* <CarouselControlsArrowsIndex
        index={currentIndex}
        total={CAROUSELS.length}
        onNext={handleNext}
        onPrevious={handlePrevious}
      /> */}
    </Card>
  );
};

export default SimpleBannerCarousel;
